<template>
  <div>
    <el-table
      :data="menuList"
      style="width: 100%; margin-bottom: 20px"
      row-key="id"
      border
      default-expand-all:false
      :tree-props="{ children: 'children' }"
    >
      <el-table-column prop="id" label="菜单编号" width="120">
      </el-table-column>
      <el-table-column prop="title" label="菜单名称"> </el-table-column>
      <el-table-column prop="icon" label="菜单图标"> </el-table-column>
      <el-table-column prop="url" label="菜单地址"> </el-table-column>
      <el-table-column prop="status" label="状态">
        <template slot-scope="scope">
          <el-button type="primary" v-if="scope.row.status == 1" @click="scope.row.status=2">启用</el-button >
          <el-button type="danger" v-else @click="scope.row.status=1">禁用</el-button>
        </template>
      </el-table-column>
      <el-table-column prop="id" label="操作">
        <template slot-scope="scope">
          <el-button type="primary" @click="emitMenu(scope.row.id)"
            >编辑</el-button
          >
          <el-button type="danger" @click="deltMenu(scope.row.id)"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import { delMenu } from "../../util/request";
import {successAlert} from "../../util/alert"
export default {
  props: ["info"],
  data() {
    return {};
  },
  computed: {
    ...mapGetters({
      menuList: "menu/getmenuList",
    }),
  },
  methods: {
    ...mapActions({
      menuListActions: "menu/menuListActions",
    }),
    emitMenu(id) {
      this.$emit("edit", id);
    },
    //删除数据
    deltMenu(id) {
      this.$confirm("确定要删除吗?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        delMenu({ id: id }).then((res) => {
          if (res.data.code == 200) {
            successAlert(res.data.msg);
            this.menuListActions();
          } 
        });
      }).catch(()=>{
        this.$message({
          type:"info",
          message:"已取消删除"
        })
      });
    },
  },
  mounted() {
    this.menuListActions();
  },
};
</script>

<style>
</style>


