<template>
  <div>
    <!-- Form -->
    <el-dialog :title="info.title" :visible.sync="info.showAdd">
      <el-form :model="form">
        <el-form-item label="菜单名称" :label-width="formLabelWidth">
          <el-input v-model="form.title" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="上级菜单" :label-width="formLabelWidth">
          <el-select v-model="form.pid" placeholder="请选择">
            <el-option label="顶级菜单" :value="0"></el-option>
            <el-option v-for="item in list" :key="item.id" :label="item.title" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item  label="菜单类型" :label-width="formLabelWidth">
          <el-radio  v-model="form.type"  :label="1" >目录</el-radio>
          <el-radio  v-model="form.type" :label="2">菜单</el-radio>
        </el-form-item>
        <el-form-item  icon label="菜单图标" v-if="form.type==1" :label-width="formLabelWidth">
          <el-select v-model="form.icon" placeholder="请选择">
            <el-option v-for="item in icon" :key="item" :label="item" :value="item"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="菜单地址" v-else :label-width="formLabelWidth">
          <el-select v-model="form.url" placeholder="请选择">
            <el-option v-for="item in url" :key="item" :label="item" :value="item"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item  label="状态" :label-width="formLabelWidth">
          <el-switch v-model="form.status"
            active-color="#409eff"
            inactive-color="grey"
            :active-value="1" :inactive-value="2"
          >
          </el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary" v-if="info.isAdd" @click="ok">添 加</el-button>
        <el-button type="primary" v-else @click="update" >修 改</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {requetAddMenu,requestMenu,editMenu} from "../../util/request"
import {successAlert,warningAlert} from "../../util/alert"
import {mapGetters,mapActions} from "vuex"
import axios from "axios"
export default {
  props: ["info"],
  data() {
    return {
      form: {
        pid: 0,
        title: "",
        icon: "",
        type: 1,
        url: "",
        status: 1,
      },
      url:[
        "menu",
        "role",
        "manger",
        "category",
        "spec",
        "goodmanag",
        "member",
        "banner",
        "seckill"
        ],
      icon:[
        "el-icon-setting",
        "el-icon-goods",
        "el-icon-bell",
        "el-icon-s-operation",
        "el-icon-s-home"
      ],
      formLabelWidth: "120px",
    };
  },
  computed:{
   ...mapGetters( {
     "list":"menu/getmenuList"
   })
  },
  methods:{
    ...mapActions({
      "MenuList":"menu/menuListActions"
    }),
    cancel(){
      this.info.showAdd=false
      this.form={
        pid: 0,
        title: "",
        icon: "",
        type: 1,
        url: "",
        status: 1,
      };
    },
    ok(){
      console.log("ok");
      this.dialogFormVisible =false;
      requetAddMenu(this.form).then(res=>{
        if(res.data.code==200){
          successAlert(res.data.msg);
          this.cancel();
          this.MenuList()
        }else{
          warningAlert(res.data.msg)
        }
      })
    },
    //通过menu中的编辑事件传递的id在添加组件
    editMenu(id){
      //发送请求
      requestMenu({id:id}).then(res=>{
        this.form=res.data.list
        this.form.id=id
      })
    },
    //修改数据
    update(){
      console.log("g");
      editMenu(this.form).then(res=>{
        if(res.data.code==200){
          successAlert(res.data.msg)
          this.cancel();
          this.MenuList()
        }else{
          warningAlert(res.data.msg)
        }
      })
    },
   
  },
  mounted(){
    this.MenuList()
  }
};
</script>

<style>
</style>


