<template>
  <div>
    <el-button type="primary" class="add" @click="add" >添加</el-button>
    <v-add :info="info" ref="add"></v-add>
    <v-list :info="info" @edit="edit($event)"></v-list>
  </div>
</template>

<script>
import vAdd from "../../pages/menu/add.vue";
import vList from "../../pages/menu/list.vue"
export default {
  data() {
    return {
      info: {
        showAdd: false,
        title: "",
        //控制编辑或添加按钮的显示和隐藏
        isAdd:true
      },
    };
  },
  methods: {
    add() {
      this.info.showAdd = true;
      this.info.title = "添加菜单";
      this.info.isAdd=true
    },
    edit(id){
        this.info.showAdd=true;
        this.info.title="编辑菜单";
        this.$refs.add.editMenu(id);
        this.info.isAdd=false
    }
  },
  components: {
    vAdd,
    vList
  },
};
</script>

<style>
.el-button {
  margin: 20px 0;
}
</style>


